import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { DosageSizeComponent } from './dosage-size.component';

const routes: Routes = [
  {
    path: '',
    component: DosageSizeComponent,
    data: {
      title: 'Dosage Size',
      status: false
    }
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class DosageSizeRoutingModule { }



