import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

@Component({
  selector: 'add-dosage',
  templateUrl: './dosage-size.component.html',
  styleUrls: [
    './dosage-size.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [MeasurementAndDosageSizesService]
})
export class DosageSizeComponent implements OnInit {
  rowsBasic = [];
  fullScreenRow = [];
  loadingIndicator = true;
  reorderable = true;
  DosageSize = {};
  DosageSizeee: any = [];

  columns = [
    { prop: 'name' },
    { name: 'Gender' },
    { name: 'Company' }
  ];

  rows = [];
  expanded = {};
  timeout: any;
  DosageSizee: any = {};
  DosageSizee2: any = {};

  rowsFilter = [];
  tempFilter = [];
  public dosages_array = [];
  public dosage_id = [];
  public dosages_values_array = [];
  @ViewChild(DatatableComponent) table: DatatableComponent;

  constructor(public _dosageSizeServices: MeasurementAndDosageSizesService) {
    this.getDosages();
  }

  ngOnInit() {
  }

  fetchBasicData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/basic.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  getDosages() {
    this._dosageSizeServices.getDosage().subscribe(response => {
      if (response.code == 200) {
        this.dosages_array = response.data;
      }
    });
  }// Get Dosages End
  getDosageValues(){
    $("#categories-fetcher").show();
    this._dosageSizeServices.getDosageSizes(this.dosage_id).subscribe(response => {
      if (response.code == 200) {
        console.log(response.data);
        $("#categories-fetcher").hide();
        $("#second-div").show();
        this.dosages_values_array = response.data;
        this.tempFilter = this.dosages_values_array;
        this.rowsFilter = this.dosages_values_array;
      }
    });
  }// Get Dosage Values End
  fetchFullScreenData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', 'assets/data/fullscreen.json');

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }

  fetchFilterData(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/company.json`);

    req.onload = () => {
      cb(JSON.parse(req.response));
    };

    req.send();
  }


  updateFilter(event) {
    const val = event.target.value.toLowerCase();

    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.name.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.table.offset = 0;
  }

  onPage(event) {
    clearTimeout(this.timeout);
    this.timeout = setTimeout(() => {
      console.log('paged!', event);
    }, 100);
  }

  fetch(cb) {
    const req = new XMLHttpRequest();
    req.open('GET', `assets/data/100k.json`);

    req.onload = () => {
      const rows = JSON.parse(req.response);

      for (const row of rows) {
        row.height = Math.floor(Math.random() * 80) + 50;
      }

      cb(rows);
    };
    req.send();
  }

  getRowHeight(row) {
    return row.height;
  }

  chech() {
    alert("Hello!!!");
  }
  //services
  addDosage = {};
  addDosageSizeServices() {
    $('.text-danger').hide();
    if(this.DosageSizee.name == "" || this.DosageSizee.name === undefined || this.DosageSizee.name.trim() == ""){
      $('#dosage-value-error').show();
      return;
    }
    this._dosageSizeServices.addDosageValues(this.dosage_id, this.DosageSizee.name).subscribe(data => {
      // this.addMeasurement=data;
      console.log(data);
      this.dosages_values_array.push(data.data);
      this.DosageSizee = {};

    });
  }
  //delete Services
  deleteDosageSizeServices(i) {
    console.log(i);
    var self = this;
    swal({
      title: 'Are you sure?',
      text: 'You wont be able to revert',
      type: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#3085d6',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Yes, delete it!'
    }).then(function (result) {
      if (result.value == true) {
        self._dosageSizeServices.deleteDosageValue(i).subscribe(response => {
          var index_number = self.dosages_values_array.findIndex(x => x.Id == i);
          self.dosages_values_array.splice(index_number, 1);
          self.rowsFilter = self.dosages_values_array;
          self.tempFilter = self.dosages_values_array;
          swal(
            'Deleted!',
            'Package has been deleted.',
            'success'
          );
        });
      }
      else {
      }
    }).catch(swal.noop);
  }
  public dosage_value_id: number = 0;
  openMyModal(Id){
    this.dosage_value_id = Id;
    var obj = this.dosages_values_array.find(x=>x.Id == Id);
    this.DosageSizee2.name = obj.Value;
  }
  updateDosage(){
    $('.text-danger').hide();
    if(this.DosageSizee2.name == "" || this.DosageSizee2.name === undefined || this.DosageSizee2.name.trim() == ""){
      $('#update-dosage-value-error').show();
      return;
    }
    var index_number = this.dosages_values_array.findIndex(x => x.Id == this.dosage_value_id);
    this.dosages_values_array[index_number].Value = this.DosageSizee2.name;
    this._dosageSizeServices.updateDosageValue(this.dosage_value_id, this.DosageSizee2.name).subscribe(response => {
      console.log(response)
    });
    this.rowsFilter = this.dosages_values_array;
    this.tempFilter = this.dosages_values_array;
  }
}



